@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
body {
  margin: 0;
  font-family: "Orbitron", sans-serif, "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: #ffffff;
}

code {
  font-family: "Orbitron", sans-serif;
}

.App {
  text-align: center;
}
#appComponent {
  width: 100%;
  height: 100%;
  background-color: black;
  color: white;
  font-family: "Orbitron", sans-serif;
}

#offcanvasNavbar {
  background-color: black !important;
  color: white;
}
#navbarStyling {
  height: 8em !important;
}
#offcanvasNavbarDropdown {
  color: white !important;
}
#menuBtn {
  background-color: #144441 !important;
}
#navbarTitle {
  color: white !important;
  border-bottom: 5px solid #04baad;
}

#descriptionComponent {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2em;
}
#descImages {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.descImgContainer {
  width: 100%;
  height: 100%;
}
.descImg {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
#descTitle {
  font-size: 3em;
  border-bottom: 5px solid #04baad;
}
#descText {
  font-size: 1.2em;
}

.infoImgContainer {
  height: 100%;
  width: 100%;
}
.infoImg {
  height: 100%;
  width: 100%;
  object-fit: contain;
}
.infoTextGroup {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* border: 1px solid black; */
  height: 100%;
  width: 100%;
}
.infoTitle {
  color: white;
  background-color: black;
  font-size: 5em;
  border-bottom: 5px solid #04baad;
}
.infoPara {
  color: white;
  background-color: black;
  font-size: 1.2em;
}

#communityComponent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1em 0 1em 0;
  height: 100%;
  width: 100%;
}

#roadmapComponent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  height: 100%;
  width: 100%;
}
.roadmapItem {
  background-color: black;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.roadmapTitle {
  font-size: 4em;
}
#lastRoadmapPara {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.roadmapPara {
  font-size: 1.4em;
  margin: 1em 0 1em 0;
  border-bottom: 5px solid #04baad;
  padding: 10px;
}

#aboutComponent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: white;
  height: 100%;
  width: 100%;
  padding: 0 10px 0 10px;
}
#aboutTitle {
  font-size: 5em;
  border-bottom: 5px solid #04baad;
}
.aboutPara {
  font-size: 1.2em;
}

#footerComponent {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  background-color: #04baad;
  color: white;
  /* border-top: 2px solid white; */
}

#faqContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 2px dashed #04baad;
}
#faqGeneralTitle {
  border-bottom: 5px solid #04baad;
  font-size: 5em;
}
.faqDiv {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 2em;
}
.faqTitle {
  font-size: 3.5em;
}
.faqPara {
  font-size: 2em;
}

#landingComponent {
  height: 100%;
  width: 100%;
}
#landingText {
  height: 100%;
  width: 100%;
  /* border: 2px solid brown; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: white;
}
#landingImgContainer {
  height: 100%;
  width: 100%;
}
#landingImg {
  height: 100%;
  width: 100%;
  object-fit: contain;
}
#landingTitle {
  font-size: 3.2em;
  border-bottom: 5px solid #04baad;
}
#landingPara {
  font-size: 1.3em;
}
#connectBtn {
  background-color: #04baad;
  font-size: 3em;
  font-weight: 500;
}
.walletPara {
  color: #04baad;
  font-weight: 900;
  font-size: 1.2em;
}
#mintBtn {
  background-color: #04baad;
  font-size: 2em;
  font-weight: 500;
  color: white;
}

